@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,700,100);
@import url(https://fonts.googleapis.com/css?family=Oswald:400,300,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700,300);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);

/*==Typography============================*/
/*==CSS Starts============================*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body{
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  color:#333;
  background-color:#fff;
}

section{
padding: 8rem 0;
margin: 0;
background: #fff;
display: inline-block;
width: 100%;
position: relative;
}

a {
transition: .3s ease-in-out;
color: #f05323;
text-decoration: none;
}

a:hover {
outline: none;
text-decoration:none;
color:#f05323;
}

a:focus {
outline:none;
outline-offset: 0;
color:#f05323;
}

h1 {
  font-size: 5vw;
}

h1, h2, h3, h4, h5, h6 {
font-family: 'Roboto', sans-serif;
color: #272727;
margin: 20px 0 10px 0;
text-transform: uppercase;
}

.root .container {
  width: auto;
}

p {
font-size: 14pt;
}

ul {
list-style: none;
margin-left: 0;
margin-top: 0;
padding: 0;
}


img {
  display: inline-block;
  width: 100%;
}

.colmn {
  position: relative;
  padding: 0 15px;
  display: flex;
}

.colmn.lg {
  width: 100%;
  flex-direction: column;
}


.header {
    position: fixed;
    top:0;
    width: 100%;
    height: 70px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
    z-index: 10;
    
}

.header.fixed {
    background-color:rgba(0, 0, 0, 0.9);
}

.header .nav-menu {
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: -100%;
    top: 0;
    text-align: center;
    width: 100%;
    height: auto;
    z-index: -1;
    transition: .3s all cubic-bezier(0.77, 0, 0.175, 1);
    background-color: #000;
    margin: 0;
    padding: 9em 0;
}

.header .nav-menu li  {
    padding: 1rem 1.5rem;
    font-size: 16px;
}

.header .nav-menu a {
   color: #ffffff;
   font-size: 1.4em;
   text-decoration: none;
}

.header .nav-menu a:hover {
    color: #f05323;
}

.logo-dark {
    display: block;
}

.logo-title {

    max-width: 150px;
}

.header .hamburger {
    color: #fff;
    padding-right: 1.5rem;
    font-size: 16px;
    position: absolute;
    left: 15px;
}
    
.header .nav-menu.opened {
    left: 0;
}
@media only screen and (max-width: 760px) {

    .header .nav-menu {
        padding-top: 9em;
        height: 100vw;
    }
    .logo-title {
        left: 1.2rem;
        max-width: 120px;
    }

    
}

.masthead {
    padding-top: calc(25rem - 4.5rem);
    padding-bottom: calc(25rem - 6.5rem);
    background: linear-gradient(180deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,1) 100%), url("https://images.unsplash.com/photo-1518773553398-650c184e0bb3?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80");
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    position: relative;
}

.ri-arrow {
    width: 42px;
    height: 42px;
    color: #ccc;
    margin-top: 1em;
}

.masthead .container {
    text-align: center;
    position: static;
}

.intro-title  {
	font-size: 10vw;
    color: #fff;
    margin: 0 0 5px 0;
	text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif;
	text-shadow: 1px 3px 11px rgba(0,0,0,.3);
    font-weight: bold;
}

.intro-subtitle  {
    font-size: 22px;
    font-weight: 100;
	text-transform: uppercase;
	letter-spacing: 3.2px;
    color: #fff;
}

.learnmore {
    display: block;
	margin-top: 4vh;
}

.masthead a {
    text-decoration: none;
}

.masthead .learnmore a {
	text-transform: uppercase;
	font-size: 1.5em;
	font-weight: normal;
	color: #fff;
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	pointer-events: all;
	cursor: pointer;
	letter-spacing: .05em;
	padding: 1em 3em;
	display: inline-block;
	background-color: transparent;
	transition: all .3s ease-in-out;
	text-align: center;
    margin-right: 2em;
	overflow: hidden;
	position: relative;
	box-shadow: inset 0 0 0 2px #f05323;
}

.masthead .learnmore a:hover {
    background-color: #f05323;
    color: #fff;
}
  
@media (min-width: 768px) {
    .masthead {
        height: 100vh;
        min-height: 40rem;
        padding-top: 4.5rem;
        padding-bottom: 0;
    }
    .masthead p {
        font-size: 1.55rem;
    }
    .intro-title  {
        font-size: 10vw;
    }
}

.skills {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 100%;
	position: relative;
    padding: 8rem 0;
    background-color: #fff;
    line-height: 1.42857143;
}

.inner {
    padding: 0 45px;
    text-align: center;
}


.inner p {
    font-size: 18px;
	font-weight: 100;
	margin-bottom: 2em;
    max-width: 380px;
}

.inner ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.inner li {
    font-size: 18px;
    font-weight: 100;
    display: list-item;
	text-align: -webkit-match-parent;
}

.skills-title {
    font-size: 2rem;
	font-weight: bold;
	margin-top: 10px;
    color: #000;
	margin-bottom: 1.5em;
}

p.list-title {
    color: #f05323;
    margin-bottom: 0;
    font-weight: 500;
}

.fa-laptop,
.fa-drafting {
    font-weight: 100;
	font-size: 5rem;
	color: #f05323;
	margin-bottom: 15px;
}
.portfolio {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 100%;
	position: relative;
    padding: 8rem 0;
    background-color: #fff;
    border: 0;
}

.portfolio a {
    color: #f05323;
    text-decoration: none;
}

.project-gird {
    padding-top: 7rem;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    max-width: 1400px;
}

.project-colmn {
    flex-wrap: wrap;
    padding: 0;
}

.project-wrap {
    width: 33%;
    flex: none;
    display: block;
    box-sizing:inherit;
    border: 0;
}


.project-inner {
    position: relative;
	overflow: hidden;
    display: flex;
    box-sizing:inherit;
    height: 100%;
}

.title-category {
    font-size: 16pt;
	color: #1b242f;
    font-weight: normal;
}

.text-category {
    font-size: 12pt;
    color:#f05323;
    font-weight: normal;
}

.portfolio-bg {
    width: 100%;
    height: 100%;
    padding-bottom: 70%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    max-width: 100%;
    transition: opacity 0.5s;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.project-wrap img {
    transition: .25s ease-in-out;
}

.project-wrap:hover .portfolio-bg{
    opacity: 0.95;
}

.project-wrap:hover img{
	transform: scale(1.1);
}

.project-wrap:hover .label-text {
    opacity: 1;
    top: 24%;
}

.project-wrap:hover .cta-btn {
    opacity: 1;
    bottom: 24%;
}

.cta-btn {
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	color: #fff;
	opacity: 0;
	border: 1px solid #f05323;
	background-color: #f05323;
	font-size: 13pt;
	margin: 0 auto;
	padding: 7px 0;
	width: 170px;
	text-align: center;
	transition: all 0.3s ease-in-out;
	z-index: 2;
}

.cta-btn:hover {
    color: #fff;
}

.label-text {
    color: #1b242f;
	opacity: 0;
	text-align: center;
	transition: all 0.3s ease-in-out;
	z-index: 2;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.project {
    position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	right: -88%;
	padding-top: 100px;
	background: rgba(255,255,255, 0.95);
	z-index: 103;
	opacity: 0;
	visibility: hidden;
	transition: all 0.5s ease-in-out;
}

.project.opened {
    right: 0;
    opacity: 1;
    visibility: visible;
}

.about {
    padding-top: 10rem;
    padding-bottom: calc(10rem - 4.5rem);
background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 10rem 0;
}
    
.about-title {
   text-transform: uppercase;
    margin-top: 0;
    font-size: 5vw;
    margin-bottom: 30px;
    font-family: 'Roboto Condensed', sans-serif;
    color: #fff;
    font-weight: bold;
}
    
.about-text {
    font-size: 2rem;
    line-height: 1.6;
    color: #fff;
}
    
.about .container {
    max-width: 970px;
    text-align: center;
}
